<template>
  <el-row class="form-bg">
    <el-col :span="10" :offset="6">
      <el-form ref="form" :rules="rules" :model="forminfo" label-width="100px">
        <el-form-item label="角色名称" prop="roleid">
          <el-select v-model="forminfo.roleid" placeholder="请选择">
            <el-option label="请选择角色" value=""></el-option>
            <el-option v-for="item in rolelist" :key="item.id" :label="item.rolename" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="管理员名称" prop="username">
          <el-input v-model="forminfo.username" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item label="管理员密码" v-if="$route.params.id"  >
          <el-input v-model="forminfo.password" placeholder="密码留空表示不修改" show-password></el-input>
        </el-form-item>
        <el-form-item label="管理员密码" prop="password" v-else>
          <el-input v-model="forminfo.password" placeholder="请输入密码" show-password></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-switch v-model="forminfo.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit">立即{{$route.params.id ? '修改':'创建'}}</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import {  getRoleList } from "@/request/role";
import {  addAdmin,editAdmin,getAdminInfo } from "@/request/admin";
export default {
  data() {
    return {
      forminfo: {
        roleid: "",
        username: "",
        password: "",
        status: 1, // 状态，1是正常，2是禁用
      },
      rolelist: [],
      rules: {
        roleid: [{ required: true, message: "请选择角色名称", trigger: "blur" }],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        username: [{ required: true, message: "请输入管理员名称", trigger: "blur" }],
      },
    };
  },
  created() {
    this.getlist();
    // 如果是修改页面，就请求当前页面的数据！
    if (this.$route.params.id) {
      this.getinfo();
    }
  },
  methods: {
    async getlist() {
      this.rolelist = await getRoleList();
    },
    async getinfo() {
      this.forminfo = await getAdminInfo(this.$route.params.id);
      this.forminfo.password = ""
    },
    submit() {
      // 验证
      // this.$refs.form.validate(valid=>{ valid 为true验证通过，false表示验证失败  })
      this.$refs.form.validate((valid) => {
        if (valid) {
          // 提交数据
          if (this.$route.params.id) {
            editAdmin(this.$route.params.id, this.forminfo);
          } else {
            addAdmin(this.forminfo);
          }
        } else {
          this.$message.warning("验证失败");
        }
      });
    },
    reset() {
      if (this.$route.params.id) {
        this.getinfo();  // 修改页面的重置其实就是做一下页面第一次进入请求数据赋值的那个事情！
      } else {
        this.$refs.form.resetFields();
      }
    },
  },
  components: {},
};

// BootStrap
</script>
<style scoped>
</style>


